import { WatchlistDataTablePriceCell } from "../../WatchlistDataTableCells/WatchlistDataTablePriceCell"
import { genericColumn } from "../../genericColumn"

function inventoryTurnoverTtm(tableName: string) {
  const title = "Inventory Turnover (LTM)"
  return genericColumn({
    endpoint: "companyRatios",
    columnKeys: ["inventoryTurnoverTtm"],
    size: 110,
    title,
    enableSorting: true,
    enableHiding: true,
    tooltip: "How often inventory is sold in a year.",
    cell: info => {
      return (
        <WatchlistDataTablePriceCell
          tooltip={`Explain the ${title}`}
          queryResult={info.row.original.financialData.companyRatios}
          onClick={() => {
            info.row.original.onChatWithCell?.({
              prompt: `What has ${info.row.original.entityInfo.name} recently reported about inventory turnover?`,
              value: info.row.original.financialData.companyRatios?.data?.inventoryTurnoverTtm,
              entityInfo: info.row.original.entityInfo,
              columnName: `${tableName}: ${title}`,
            })
          }}
          value={info.row.original.financialData.companyRatios?.data?.inventoryTurnoverTtm}
        />
      )
    },
  })
}

export { inventoryTurnoverTtm }
